import React, { Component } from 'react'
import Myswiper from "../../common/myswiper/myswiper"
import Header from "../components/header"
import {getArtical} from "../../../services/homeService"
import {NavLink} from 'react-router-dom'
import "./style.scss"
import Myiscorll from "../../common/myiscroll/myiscroll"

const Btn = (props)=>(
 <p className="slideBnt">
    <span 
     className={(props.slideBtn===0)?"active l":"l"} 
     onClick={props.silde.bind(this,0)}>讲义</span>
    <span 
     className={(props.slideBtn===1)?"active r":"r"}
     onClick={props.silde.bind(this,1)}>教材</span>
</p>
)


const Item = (props)=>(
  <>
    {props.data.map((item,index)=>(
       <div key={index} className="box">
        <img src={item.img?item.img:"/img/nothing.gif"} alt="图片"/>
        <div className="right">
           <h4>{item.title}</h4>
           <p className="brief">{item.tags}</p>
           <p className="btn">
            {props.index===1&&(
            <>
            <NavLink to={{pathname: "/textbook/preview/1", state:item}}>
                <span className="span"
                id="every">预览</span>
            </NavLink>
            <a className="span" href="/img/loading.jpg"  download={item.title}>下载</a>
            </>)}

            {props.index===2&&(
            <a className="span" onClick={props.toast}>申请</a>)}
          </p>
        </div>
        </div> 
    ))
    }
    </>
) 


export default class textbook extends Component {
    constructor(props){
        super(props);
        this.state = {
            slideBtn:0,
            textBook:[
                {
                    img:"//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCOxvPSMfXfMeaNXtUSribMEFjDvOPbWNqvOrcbwkH8vjfZTJnQFcY8womat5fslulo/356?tp=webp",
                    title:"悲惨世界守护者",
                    tags:"没有时间的概念..."
                }
            ],
            handouts:[
                {
                    img:"//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCOxvPSMfXfMeaNXtUSribMEFjDvOPbWNqvOrcbwkH8vjfZTJnQFcY8womat5fslulo/356?tp=webp",
                    title:"悲惨世界守护者",
                    tags:"没有时间的概念..."
                }
            ]
        }
    }

    textbookAction(index){ 
        window.swiper.slideTo(index, 1000, false);
        this.setState({
            slideBtn:index
        });
    }

   getHandouts(){
    //    248
    getArtical(248)
    .then(data=>{
        this.setState({
            handouts: data.data
        })
    })
   }

   getTextBook(id){
    //    254
    getArtical(id)
    .then(data=>{
        this.setState({
            textBook:[
                ...data.data,
                ...this.state.textBook
            ]
        })
    })
   }

   back(){
      window.location.href = "/home";
   }

    render() {
        let {slideBtn,textBook,handouts} = this.state;
        return (
            <div id="textbook">
                <Header
                 back={this.back.bind(this)}
                 centent={
                 <Btn
                  slideBtn={slideBtn}
                  silde={(index)=>{
                    this.textbookAction.call(this,index)}}
                 />}>
                </Header>
                 {/* 按钮 */}

                <Myswiper>
                <div className="swiper-slide swiper-no-swiping"
                style={{overflow:"auto"}}>
                      <Item data={textBook||[]}
                       index={1}/>
                </div>

                <div className="swiper-slide swiper-no-swiping">
                  <Myiscorll top="0px">
                    <Item data={handouts||[]}
                    index={2}
                    toast={()=>window.mui.toast("申请已提交...")}/>
                   </Myiscorll>
                 </div>
                </Myswiper>
            </div>
        )
    }

    componentDidMount(){
        // 初始化数据
        this.getHandouts(248);
        this.getTextBook(254);
    }
}
